import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Psalm } from '@app/psalm/model/psalm';


@Component( {
    selector: 'psalmMultiContainer',
    templateUrl: './psalmMultiContainer.component.html'
} )
export class PsalmMultiContainerComponent implements OnInit {
    @Input() psalms: Psalm[] = [];
    @Input()
    set psalm(psalm: Psalm) {
        this.psalms.push(psalm);
    }

    constructor() { }

    ngOnInit(): void {
    }
    
    expandPsalm(index: number): void {
        if(this.psalms[index].predecessor.length === 0)
            return;
        
        for(let psalm of this.psalms[index].predecessor) {
            this.psalms.push(psalm);
        }
        this.psalms.splice(index, 1);
    }
  
}
